@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Open Sans", sans-serif;
}

.Home {
  height: 100vh;
  position: relative;
}
.Home header {
  display: flex;
  background-color: #141A46;
  position: relative;
  top: 0;
  left: 0;
  font-size: 2.5rem;
  justify-content: space-between;
  align-items: center;
}
.Home nav ul {
  display: flex;
  align-items: center;
  list-style-type: none;
}
.Home nav ul li {
  display: flex;
}
.Home nav ul li a {
  display: inline-block;
  padding: 1rem;
  font-size: 2rem;
  transition-property: background-color color font-weight;
  transition-duration: 300ms;
  margin: 0 1rem;
  border-radius: 10px;
  text-decoration: none;
  color: white;
  letter-spacing: 0.1rem;
  font-weight: 300;
  text-transform: uppercase;
}
.Home nav ul li a:hover {
  color: black;
  background-color: white;
  font-weight: 400;
}
.Home h1 {
  font-size: 3.5rem;
  padding: 1rem;
  font-weight: 300;
  color: #EC8B5E;
}
.Home .splash-container {
  overflow: hidden;
  height: 50vh;
}
.Home .splash-container h1 {
  display: inline-block;
  position: absolute;
  top: 15%;
  left: 30%;
  transform: translateX(-50%);
  z-index: 10;
  font-weight: 500;
  font-size: 13rem;
  color: #EC8B5E;
  -webkit-filter: drop-shadow(-1px 1px 3px black);
          filter: drop-shadow(-1px 1px 3px black);
}
.Home .splash-container img {
  max-width: 100%;
  transform: translateY(-30%);
  -webkit-filter: blur(0px) brightness(90%);
          filter: blur(0px) brightness(90%);
  z-index: -1;
}
.Home .home-picker {
  margin-top: 4rem;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  width: 60%;
  font-size: 2.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Home .home-picker select {
  font-size: 2.5rem;
  width: 40%;
}
.Home .home-picker button {
  width: 20%;
  font-size: 2.5rem;
  border: 1px solid black;
  margin-top: 2rem;
  background-color: #EC8B5E;
  border-radius: 15px;
}
.Home .google-signin {
  font-size: 4rem;
  position: relative;
  left: 50%;
  font-size: 3rem;
  transform: translateX(-50%);
  background-color: white;
  border: 2px solid dodgerblue;
}
.Home .contact-container {
  background-color: #141A46;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  color: white;
  display: flex;
  justify-content: center;
  font-size: 1.5rem;
  text-align: center;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Open Sans", sans-serif;
}

.chat-picker {
  border: 1px solid #EC8B5E;
  background-color: #141A46;
  padding: 1rem;
  display: flex;
  align-items: center;
  height: 5vh;
  flex: 0.95 1;
  height: auto;
}
.chat-picker label {
  font-size: 3rem;
  font-weight: 300;
  letter-spacing: 0.1rem;
}
.chat-picker .school-container {
  font-size: 3rem;
  text-align: center;
}
.chat-picker .class-container {
  text-align: center;
  font-size: 3rem;
}
.chat-picker select {
  -webkit-filter: drop-shadow(0px 0px 5px black);
          filter: drop-shadow(0px 0px 5px black);
  background-color: #141A46;
  width: 30%;
  font-size: 2.5rem;
  color: white;
  font-weight: 300;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Open Sans", sans-serif;
}

.Chat {
  background-color: white;
  height: 100vh;
  position: relative;
  overflow: hidden;
  color: white;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
}
.Chat .top-bar {
  display: flex;
  width: 100%;
  background-color: #141A46;
  color: white;
}
.Chat .top-bar .class-label-container {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.Chat .top-bar .class-label-container span {
  font-size: 3rem;
  text-align: center;
}
.Chat .top-bar .chat-picker {
  display: flex;
  flex-direction: column;
}
.Chat .top-bar .chat-picker select {
  width: 100%;
}
.Chat .top-bar .chat-picker button {
  display: none;
}
.Chat .top-bar .sidebar-container {
  text-align: center;
}
.Chat .top-bar .sidebar-container .menu-icon {
  font-size: 6rem;
  color: inherit;
}
.Chat .top-bar .sidebar-container:hover .side-menu {
  left: 0;
}
.Chat .top-bar .sidebar-container .side-menu {
  text-align: justify;
  padding: 2rem;
  height: 100vh;
  background-color: #141A46;
  position: absolute;
  z-index: 100;
  box-shadow: -2px 0px 5px black;
  top: 0;
  left: -20vw;
  width: 20vw;
  transition-property: left;
  transition-duration: 300ms;
}
.Chat .chat-component {
  position: relative;
  bottom: 0;
  left: 0;
  padding: 0 5vw;
  height: 100%;
}
.Chat .chat-component .chat-window {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 80vh;
  height: auto;
  overflow-y: scroll;
  overflow-x: hidden;
  max-height: 80vh;
}
.Chat .chat-component .chat-window .beginning-label {
  font-size: 1.5rem;
  color: black;
  position: relative;
  text-align: center;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
.Chat .chat-component .chat-window .message {
  display: flex;
  align-items: center;
  padding: 0rem 1rem;
  text-align: center;
  width: 100%;
  color: white;
  font-size: 1.6rem;
  padding: 1.2rem;
  margin: 0.2rem 0;
  text-align: justify;
  color: black;
}
.Chat .chat-component .chat-window .message img {
  width: 50px;
  height: auto;
  margin-right: 1rem;
}
.Chat .chat-component .chat-window .message:hover {
  background-color: #b8b8b8;
}
.Chat .chat-component .form-container {
  position: absolute;
  bottom: 0;
  display: flex;
  width: 100%;
  margin-top: 1rem;
  justify-content: center;
}
.Chat .chat-component .form-container input, .Chat .chat-component .form-container button {
  font-size: 2.5rem;
}
.Chat .chat-component .send-box, .Chat .chat-component .send-box:focus-within {
  font-size: 1.5rem;
  flex: 0.6 1;
  background-color: #a5a5a5;
  border-radius: 30px;
  margin-right: 3rem;
  border: 1px solid black;
}
.Chat .chat-component .send-btn {
  font-size: 1.5rem;
  flex: 0.1 1;
  background-color: #EC8B5E;
  border: none;
  padding: 0.5rem;
  border-radius: 30px;
  transition: background-color 300ms;
}
.Chat .chat-component .send-btn:hover {
  background-color: #879fb8;
}
